<template>
    <div class="foodDetail_container">
        <heade-top :head-title="description" go-back="true"></heade-top>
        <section class="foodDetail_img">
            <img :src="imageBaseUrl+image_path" alt="">
        </section>
        <p class="foodDetail_tx">{{description}}</p>
        <section class="fooodDetail_container_title">
            <p>{{name}}</p>
            <div class="pinfen">
                <span>评分</span>
                <sating-star :rating='rating' class="pinfen_img"></sating-star>
                <span>{{rating.toFixed(1)}}</span>
            </div>
            <p class="xiaoshou">
                <span>{{rating_count}}月售</span>
                <span>售价 ¥{{foods.specfoods[0].price}}</span>
                <span v-if="foods.specfoods.length">起</span>
            </p>
            <p class="pinglun">
                <span>评论数{{rating_count}}</span>
                <span>好评率{{satisfy_rate}}%</span>
            </p>
        </section>
    </div>
</template>

<script>
import headeTop from '../../../components/header/header'
import {getImgPath} from '../../../components/common/mixin'
import {mapState} from 'vuex'
import satingStar from '../../../components/common/satingStar'
 import {imageBaseUrl} from '../../../config/env'//图片服务器地址
export default {
    components: {
        headeTop,
        satingStar
    },mixins: [getImgPath],
    data()
    {
        return{
            imageBaseUrl,
            image_path:null,
            description:null,
            month_sales:null,
            name:null,
            rating:null,
            rating_count:null,
            satisfy_rate:null,
            foods:null,
            shopId:null,

        }
    },created () {
        this.image_path=this.$route.query.image_path;
        this.description=this.$route.query.description;//食品标题
        this.month_sales=this.$route.query.month_sales;//食品id
        this.name=this.$route.query.name;
        this.rating=this.$route.query.rating;//评分
        this.rating_count=this.$route.query.rating_count;//每月销售
        this.satisfy_rate=this.$route.query.satisfy_rate;//好评玉
        this.foods=this.$route.query.foods;//详情
        this.shopId=this.$route.query.shopId;//商品id
    }
}
</script>

<style lang="scss" scoped>
@import '../../../style/mixin.scss';
.foodDetail_container{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 12;
    background-color: #fff;
}
.foodDetail_img{
    margin-top: 1.8rem;

    img{
        @include wh(100%,100%)
    }
}
.foodDetail_tx{
    @include sc(0.7rem,#999);
    padding:0.4rem;
}
.fooodDetail_container_title{
    padding:0.4rem;
}
.pinfen{
    display: flex;
    .pinfen_img{
        @include wh(2rem,1.2rem);
        margin-top: 0.2rem;
    }
    span:nth-of-type(1)
    {
        padding-right: 0.3rem;
        @include sc(0.7rem,#666);
    }
    span:nth-of-type(2)
    {
        padding-left: 0.3rem;
        @include sc(0.5rem,red)
    }
}
.xiaoshou{
    display: flex;
    span{
        font-size: 0.6rem;
        color:#666;
   }
   span:nth-of-type(2)
   {
       padding-left: 0.3rem;
       color:red
   }
    span:nth-of-type(3)
   {
       color:red
   }

}
.pinglun{
    margin-top: 0.3rem;
    span{
        @include sc(0.6rem,#666);
    }
}
</style>



